<template>
  <question :msg="question[question_index].msg"
            :image="question[question_index].image"
            :ans1="question[question_index].ans1"
            :ans2="question[question_index].ans2"
            :total="totalCnt"
            @next="next">
  </question>
</template>

<script>
import score from './score'

export default {
  name: 'Query',
  
  methods: {
    next(ans) {
      if (this.question_index == this.question.length) {
        var score_index = score(this.answer)
        alert(score_index)
      } else {
        this.question_index++
        this.answer += '/' + ans;
      }
    }
  },

  data: () => ({
    totalCnt: 4,
    answer: "",
    question_index: 0,
    question: [
      {
        msg: "msg 1",
        ans1: "yes",
        ans2: "no",
      },
      {
        msg: "msg 2",
        ans1: "yes",
        ans2: "no",
      },
      {
        msg: "msg 3",
        ans1: "yes",
        ans2: "no",
      },
      {
        msg: "msg 4",
        ans1: "yes",
        ans2: "no",
      }
    ],
  }),
}
</script>
