<template>
  <div>
    <p>{{ name }}</p>
    <img v-bind:src="require(`@/assets/${image}`)" alt="test image">
    <p>{{ bigDesc }}</p>
    <p>{{ smallDesc }}</p>
  </div>
</template>

<script>

export default {
  name: 'Answer',
  props: {
    id: String,
  },
  setup(props) {
    var name = ""
    var image = "logo.png"
    var bigDesc = ""
    var smallDesc =""

    if (props.id == "aaa") {
      name = "녹턴"
      image = "1.png"
      bigDesc = "불끄는걸 좋아함"
      smallDesc = "허허허허"
    }

    return {
        name, image, bigDesc, smallDesc
    }
  },
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>