import { createApp } from 'vue'
import vuetify from './plugins/vuetify'
import Particles from 'particles.vue3'
import App from './App.vue'
import { createRouter, createWebHashHistory } from 'vue-router'
import HelloWorld from './components/HelloWorld'

import MovieQuery from './components/Theme/Movie/Query'
import MovieAnswer from './components/Theme/Movie/Answer'

import LolQuery from './components/Lol/Query'
import LolAnswer from './components/Lol/Answer'

import questionOnlyAns from './components/Question/QuestionOnlyAns'
import question from './components/Question'
import Theme from './components/Theme'


import socialButton from './components/Button/SocialButton'

const routes = [
    { path: '/hello', component: HelloWorld },
// querys
    { path: '/q/movie', component: MovieQuery},
    { path: '/lol', component: LolQuery},

// answers
    { path: '/movie/:id', component: MovieAnswer, props: true },
    { path: '/lol/:id', component: LolAnswer, props: true },

// theme
    { path: '/:theme', component: Theme, props: true }
]

const router = createRouter({
    history: createWebHashHistory(),
    routes
  })

const app = createApp(App)
app.use(router);
app.use(vuetify)
app.use(Particles)

app.component('Question', question)
app.component('QuestionOnlyAns', questionOnlyAns)
app.component('SocialButton', socialButton)

app.mount('#app')
