<template>
  <div>
    <div>
      <v-btn class="questionOnlyAnsBtn" @click="select(1)" color="primary" dark>
        {{ ans1 }}
        <v-icon dark right>
          mdi-checkbox-marked-circle
        </v-icon>
      </v-btn>
    </div>

    <p> vs </p>

    <div>
      <v-btn class="questionOnlyAnsBtn" @click="select(2)" color="red" dark>
        {{ ans2 }}
        <v-icon dark right>
          mdi-cancel
        </v-icon>
      </v-btn>
    </div>

    <div class="ldBar no-label" id="mybar"
          style="width:80%;"
          data-aspect-ratio="none"
          data-value="0">
    </div>

  </div>
</template>

<script>
export default {
  name: 'QuestionOnlyAns',
  props: {
    ans1: String,
    ans2: String,
    total: Number
  },

  data: () => ({
    current: 0,
  }),

  methods: {
    select(ans) {
      this.$emit("next", ans)
      this.current = this.current + 1

      var myBar = new window.ldBar("#mybar");
      myBar.set(this.current * 100 / this.total);
    }
  }
}
</script>

<style>
.questionOnlyAnsBtn {
  width: 80%;
  height: 40vh !important;
  padding-left: 10%;
  padding-right: 10%;
  margin-bottom: 1vh;
  margin-top: 1vh;
}

/* for progress bar */
.ldBar.no-label .ldBar-label {
  display: none;
}

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>