<template>
  <question :msg="question[question_index].msg" :image="question[question_index].image" :ans1="question[question_index].ans1" :ans2="question[question_index].ans2" @next="next"></question>
</template>

<script>
import score from '../functions/score'

export default {
  name: 'HelloWorld',
  methods: {
    next(ans) {
      if (this.question_index == this.question.length) {
        var score_index = score(this.answer)
        alert(score_index)
      } else {
        this.question_index++
        this.answer += '/' + ans;
      }
    }
  },

  data: () => ({
    answer: "",
    question_index: 0,
    question: [
      {
        msg: "msg 1",
        image: "1.png",
        ans1: "yes",
        ans2: "no",
      },
      {
        msg: "msg 2",
        image: "2.png",
        ans1: "yes",
        ans2: "no",
      },
      {
        msg: "msg 3",
        image: "3.png",
        ans1: "yes",
        ans2: "no",
      },
      {
        msg: "msg 4",
        image: "4.png",
        ans1: "yes",
        ans2: "no",
      }
    ],
  }),
}
</script>
