<template>
  <questionOnlyAns
            :image="question[question_index].image"
            :ans1="question[question_index].ans1"
            :ans2="question[question_index].ans2"
            :total="totalCnt"
            @next="next">
  </questionOnlyAns>
</template>

<script>
import score from './score'

export default {
  name: 'Query',
  
  methods: {
    next(ans) {
      if (this.question_index == this.question.length - 1) {
        this.answer += '/' + ans;
        var score_index = score(this.answer)
        alert(score_index)
      } else {
        this.question_index++
        this.answer += '/' + ans;
      }
    }
  },

  data: () => ({
    totalCnt: 6,
    answer: "",
    question_index: 0,
    question: [
      {ans1: "답변1", ans2: "답변2"},
      {ans1: "답변1", ans2: "답변2"},
      {ans1: "답변1", ans2: "답변2"},
      {ans1: "답변1", ans2: "답변2"},
      {ans1: "답변1", ans2: "답변2"},
      {ans1: "답변1", ans2: "답변2"}
    ],
  }),
}
</script>
