<!-- https://codepen.io/laurenclark/pen/hGiqo -->
<template>
    <div class="footer-social-icons">
        <ul class="social-icons">
            <li class="social-icon"><i class="fa-facebook">facebook</i></li>
            <li class="social-icon"> <i class="fa-twitter">twitter</i></li>
            <li @click="kakaoLink" class="social-icon"> <i class="fa-kakao">kakaotalk</i></li>
            <li @click="copyLink" class="social-icon"> <i class="fa-copy">copylink</i></li>
        
        </ul>
    </div>
</template>

<script>
export default {
  name: 'SocialButton',
  props: {
    title: String,
    // desc: String,
  },

  methods: {
    copyLink() {
      const t = document.createElement("textarea");
      document.body.appendChild(t);
      t.value = window.location.href;
      t.select();
      document.execCommand('copy');
      document.body.removeChild(t);
      alert("복사 되었습니다.")
    },
    kakaoLink() {
      window.Kakao.Link.sendDefault({
        objectType: 'feed',
        content: {
          title: this.title,
          description: '',
          imageUrl:
            'https://www.ghibli.jp/gallery/mimi001.jpg',
          link: {
            mobileWebUrl: window.location.href,
            webUrl: window.location.href,
          },
        },
      })
    }
  }
}
</script>

<style>
@import url(https://fonts.googleapis.com/css?family=Lato);

.footer-social-icons {
    display:block;
    margin: 0 auto;
}
.social-icon {
    color: #fff;
    padding:10px 14px;
    -o-transition:.5s;
    -ms-transition:.5s;
    -moz-transition:.5s;
    -webkit-transition:.5s;
    transition: .5s;
}
ul.social-icons {
    margin-top: 10px;
    padding-left: 0px;
}
.social-icons li {
    vertical-align: top;
    display: inline;
    height: 100px;
}
.social-icons a {
    color: #fff;
    text-decoration: none;
}

.fa-facebook:hover {
    background-color: #3d5b99;
}

.fa-twitter:hover {
    background-color: #00aced;
}

.fa-kakao:hover {
    background-color: #FFEB34;
}

.fa-copy:hover {
    background-color: #551A8B;
}

</style>